//TOP10
export function setOption1(countryAddConfirmRankList) {
	let yAxis = [];
	let xAxis = [];
	for (let val of countryAddConfirmRankList) {
		xAxis.push(val.nation);//
		yAxis.push(val.addConfirm);//
	}
	let option1 = {
		color: ["#2f89cf"],
		tooltip: {
			trigger: "axis",
			axisPointer: {
				// 
				type: "shadow" // 'line' | 'shadow'
			}
		},
		// 
		grid: {
			left: "0%",
			top: "10px",
			right: "3%",
			bottom: "4%",
			containLabel: true
		},
		xAxis: [{
			type: "category",
			data: xAxis,
			axisTick: {
				alignWithLabel: true
			},
			//  
			axisLabel: {
				color: "rgba(255,255,255,.6) ",
				fontSize: "15"
			},
			// x
			axisLine: {
				show: false
			}
		}],
		yAxis: [{
			type: "value",
			//  
			axisLabel: {
				color: "rgba(255,255,255,.6) ",
				fontSize: 10
			},
			// y2
			axisLine: {
				lineStyle: {
					color: "rgba(255,255,255,.1)",
					width: 2
				}
			},
			// y
			splitLine: {
				lineStyle: {
					color: "rgba(255,255,255,.1)"
				}
			}
		}],
		series: [{
			name: "",
			type: "bar",
			data: yAxis,
			barWidth: "35%",
			itemStyle: {
				barBorderRadius: 5
			}
		}]
	}
	return option1
}
//
export function setOption2(foreignList) {
	let yAxis1 = [];
	let rate = [];
	let yAxis2 = [];
	let countryList = [0, 1, 7, 13, 41]//0 1  7 13 41
	for (let i of countryList) {
		yAxis1.push(foreignList[i].name);
		let cureRate = ((foreignList[i].heal / foreignList[i].confirm) * 100).toFixed(1)
		rate.push(cureRate);
		yAxis2.push(foreignList[i].heal);
	}
	let myColor = ["#1089E7", "#F57474", "#56D0E3", "#F8B448", "#8B78F6"];
	let option = {
		grid: {
			top: "10%",
			left: "22%",
			bottom: "10%"
			// containLabel: true
		},
		// x
		xAxis: {
			show: false
		},
		yAxis: [
			{
				type: "category",
				inverse: true,
				data: yAxis1,
				// y
				axisLine: {
					show: false
				},
				// 
				axisTick: {
					show: false
				},
				// 
				axisLabel: {
					color: "#fff"
				}
			},
			{
				data: yAxis2,
				inverse: true,
				// y
				axisLine: {
					show: false
				},
				// 
				axisTick: {
					show: false
				},
				// 
				show: false,
				axisLabel: {
					color: "#fff"
				}
			}
		],
		series: [
			{
				name: "",
				type: "bar",
				data: rate,
				yAxisIndex: 0,
				// 
				itemStyle: {
					barBorderRadius: 20,
					// color 
					color: function (params) {
						// params 
						// console.log(params);
						// dataIndex 
						return myColor[params.dataIndex];
					}
				},
				// 
				barCategoryGap: 50,
				//
				barWidth: 10,
				// 
				label: {
					show: true,
					position: "inside",
					// {c}    data
					formatter: "{c}%"
				}
			},
			{
				name: "",
				type: "bar",
				barCategoryGap: 50,
				barWidth: 15,
				yAxisIndex: 1,
				data: [100, 100, 100, 100, 100],
				itemStyle: {
					color: "none",
					borderColor: "#00c1de",
					borderWidth: 3,
					barBorderRadius: 15
				}
			}
		]
	};

	return option
}
